import React from 'react'
import 'antd/dist/antd.css';
import { Tabs } from 'antd';
import { Link } from 'react-router-dom';

const { TabPane } = Tabs;


class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }

    }
    render() {
        return (
            <div>
                <Tabs defaultActiveKey="1">
                    <TabPane tab={<Link to="/">Home</Link>} key="1" />
                    <TabPane tab={<Link to="/Page1">Page1</Link>} key="2" />
                    <TabPane tab={<Link to="/Page2">Page2</Link>} key="3" />
                    <TabPane tab={<Link to="/Page3">Page3</Link>} key="4" />
                </Tabs>
            </div>
        )
    }
}
export default Header